@font-face {
  font-family: Novelist;
  src: url("Novelist.c5bbe7ca.otf");
  font-weight: normal;
}

:root {
  --color-primary: #212122;
  --color-secondary: #eee;
}

* {
  box-sizing: border-box;
}

body {
  color: #333;
  flex-direction: column;
  height: 100vh;
  margin: 0;
  font: 14px / 1.2 Arial, sans-serif;
  display: flex;
  position: relative;
}

h1, h2, h3, h4 {
  font-family: Roboto Condensed, Arial, sans-serif;
}

h2 {
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: 400;
  line-height: 1.2;
}

h4 {
  margin-bottom: 4px;
  font-size: 16px;
  font-weight: 700;
  line-height: 19px;
}

.container {
  max-width: 930px;
  margin: 0 auto;
  padding: 0 16px;
}

.content {
  flex-basis: 100%;
}

.header {
  background: #212122;
  width: 100%;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 16px;
  display: flex;
}

@media screen and (width >= 768px) {
  .header__buttons {
    display: none;
  }
}

.header__button {
  background: none;
  border: 0;
  width: 30px;
  height: 30px;
  margin-right: 20px;
}

.header__button:last-of-type {
  margin-right: 0;
}

.header__button_search {
  background-image: url("search.90aecc8a.svg");
}

.header__button_profile {
  background-image: url("profile.94fc89cf.svg");
}

.header__button_menu {
  background-image: url("menu.a11fae8a.svg");
}

.header__nav {
  display: none;
}

@media screen and (width >= 768px) {
  .header__nav {
    align-items: center;
    gap: 20px;
    display: flex;
  }
}

.header__sign-in {
  color: #fff;
  background: none;
  border: 1px solid #ccc;
  border-radius: 61px;
  height: 38px;
  padding: 0 16px;
}

.header__right {
  display: none;
}

@media screen and (width >= 768px) {
  .header__right {
    align-items: center;
    display: flex;
  }
}

.header__nav-link {
  color: #fff;
  text-decoration: none;
}

.header__left {
  align-items: center;
  gap: 20px;
  display: flex;
}

.cards {
  grid-template-columns: 1fr 1fr;
  gap: 20px 10px;
  width: 100%;
  display: grid;
}

@media screen and (width >= 768px) {
  .cards {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.card {
  text-align: center;
  position: relative;
}

.card__img {
  width: 100%;
}

.card__price_old {
  color: #aeb5b9;
  position: relative;
}

.card__price_old:after {
  content: "";
  background: #aeb5b9;
  width: 100%;
  height: 1px;
  position: absolute;
  top: 9px;
  left: 0;
}

.card__sale {
  background-image: url("sale.66299b30.png");
  background-size: cover;
  width: 30px;
  height: 30px;
  position: absolute;
  top: 10px;
  left: 10px;
}

.order-background {
  background: #00000080;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.order {
  background: #fff;
  width: 600px;
  height: 600px;
  padding: 32px 16px;
}

.order__input {
  border: 1px solid #999;
  width: 100%;
  height: 38px;
  margin-bottom: 20px;
  padding: 0 10px;
}

.order__input:focus {
  border-color: #0f8fee;
  outline: none;
}

.order__button {
  color: #fff;
  background: #0f8fee;
  border: none;
  width: 100%;
  height: 38px;
}

.order__button:active {
  background: #3781d0;
  transform: translate(1px, 1px);
}

.select {
  appearance: none;
  background-image: url("arrow.6f6cc518.svg"), linear-gradient(to right, #999 1px, #0000 1px);
  background-position: 100%;
  background-repeat: no-repeat;
  background-size: 28px;
  border: 1px solid #999;
  border-radius: 0;
  width: 100%;
  height: 38px;
  margin-bottom: 20px;
  padding: 0 10px;
}

.order__pickup {
  border: 1px solid #999;
  margin-bottom: 20px;
  display: flex;
}

.order__pickup label {
  text-align: center;
  color: #666;
  width: 50%;
  height: 38px;
  line-height: 38px;
  display: inline;
}

.order__pickup label:first-of-type {
  border-right: 1px solid #999;
}

.order__pickup input[type="radio"] {
  display: none;
}

.order__radio:checked + label {
  color: #333;
  background: #eee;
}

.info {
  background-color: var(--color-secondary);
  flex-direction: column;
  justify-content: center;
  min-height: 130px;
  margin-top: 80px;
  padding: 32px 0;
  display: flex;
}

.info__container {
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 64px;
  width: 100%;
  display: grid;
}

.info__card {
  color: #333;
  align-items: center;
  gap: 16px;
  display: flex;
}

.info__img {
  object-fit: cover;
  width: 50px;
  height: 50px;
}

.contacts {
  background-color: var(--color-primary);
  flex-direction: column;
  justify-content: center;
  min-height: 230px;
  padding: 16px 0;
  display: flex;
}

.contacts__container {
  flex-direction: column;
  justify-content: center;
  gap: 16px;
  width: 100%;
  height: 100%;
  display: flex;
}

.contacts__items {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  gap: 16px;
  display: flex;
}

.contacts__socials, .contacts__apps {
  gap: 8px;
  display: flex;
}

.contacts__socials-link, .contacts__apps-link {
  transition: transform .2s;
  display: inline-block;
}

.contacts__socials-link:hover, .contacts__apps-link:hover {
  transform: scale(1.05);
}

.contacts__logo {
  filter: invert(100) grayscale();
  display: none;
}

@media screen and (width >= 768px) {
  .contacts__logo {
    display: inline;
  }
}

.contacts__divider {
  background-color: #666;
  border: none;
  width: 100%;
  height: 1px;
}

.contacts__copyright {
  color: #999;
  flex: 1;
  line-height: 20px;
}

